import { h, Component } from 'preact'
import { Router } from 'preact-router'
import { Link } from 'preact-router/match'

import Anchor from '../anchor'
import style from './style.css'

import Home from '../../routes/home'
import Portfolio from '../../routes/portfolio'

export default class Layout extends Component {
  constructor() {
    super()
    this.handleRoute = this.handleRoute.bind(this)
    this.toggleDrawer = this.toggleDrawer.bind(this)
  }

  handleRoute(event) {
    this.currentUrl = event.url
  }

  toggleDrawer() {
    document.querySelector('.mdl-layout').MaterialLayout.toggleDrawer()
  }

  render() {
    return (
      <div className="mdl-layout mdl-js-layout mdl-layout--fixed-header">
        {/* Header */}
        <header className={`${style.header} mdl-layout__header`}>
          <div className="mdl-layout__header-row">
            <span className="mdl-layout-title">Christoforus Benvenuto</span>
            <div className="mdl-layout-spacer"></div>
            <nav className="mdl-navigation mdl-layout--large-screen-only">
              <Link className="mdl-navigation__link" activeClassName="active" href="/">Home</Link>
              <Link className="mdl-navigation__link" activeClassName="active" href="/portfolio">Portfolio</Link>
            </nav>
          </div>
        </header>
        {/* Header */}
        {/* Drawer */}
        <div className={`${style.drawer} mdl-layout__drawer`}>
          <span className="mdl-layout-title">Menu</span>
          <nav className="mdl-navigation">
            <Link className="mdl-navigation__link" activeClassName="active" onClick={this.toggleDrawer} href="/">Home</Link>
            <Link className="mdl-navigation__link" activeClassName="active" onClick={this.toggleDrawer} href="/portfolio">Portfolio</Link>
          </nav>
        </div>
        {/* Drawer */}
        {/* Content */}
        <main ref={(element) => { this.content = element }} className={`${style.content} mdl-layout__content`}>
          <Router onChange={this.handleRoute}>
            <Home path="/" default />
            <Portfolio path="/portfolio" />
          </Router>
        </main>
        {/* Content */}
        {/* Footer */}
        <footer className={`${style.footer} mdl-mega-footer`}>
          <div className="mdl-mega-footer__top-section">
            <div className="mdl-mega-footer__left-section">
              <Anchor href="https://www.linkedin.com/in/cbenv">LinkedIn</Anchor>
              <Anchor href="https://github.com/cbenv">GitHub</Anchor>
              <a href="mailto:christoforus@benvenuto.me">Email</a>
            </div>
            <div className="mdl-mega-footer__right-section">
              2018  Christoforus Benvenuto
            </div>
          </div>
        </footer>
        {/* Footer */}
      </div>
    )
  }
}



